import config from '../config/config';
var handleResponse = function (res) {
    if (res.status === 401) {
        throw 'Auth failure';
    }
    else if (!res.ok) {
        throw 'Fetch failed';
    }
    else {
        return res;
    }
};
export var logout = function () {
    return fetch(config.apiServiceUrl + "/logout")
        .then(handleResponse)
        .then(function () { return true; });
};
export var login = function (password) {
    return fetch(config.apiServiceUrl + "/login", {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json',
        },
        credentials: 'include',
        body: JSON.stringify({ password: password })
    })
        .then(handleResponse)
        .then(function () { return true; });
};
export var getPayments = function () {
    return fetch(config.apiServiceUrl + "/payments", {
        credentials: 'include',
    })
        .then(handleResponse)
        .then(function (res) { return res.json(); });
};
export var getAllCategories = function () {
    return fetch(config.apiServiceUrl + "/categories", {
        credentials: 'include',
    })
        .then(handleResponse)
        .then(function (res) { return res.json(); });
};
export var savePayment = function (payment) {
    return fetch(config.apiServiceUrl + "/payments", {
        method: 'POST',
        headers: {
            'Content-Type': 'application/json'
        },
        credentials: 'include',
        body: JSON.stringify(payment)
    }).then(handleResponse);
};
